import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import UserNavbar from './components/navbar/userNavbar';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import CategoryDetail from './components/detail/categoryDetail';
import OwnerDetail from './components/detail/ownerDetail';
import CategoryAdd from './components/forms/categoryAdd';

class App extends React.Component {
  render() {
    return (
      <div>
        <Router>
          <UserNavbar />
          <br/>
          <br/>
            <div className="container">
              <Switch>
                <Route path="/owner/:id" exact component={OwnerDetail} />
                <Route path="/category/:id" exact component={CategoryDetail} />
              </Switch>
          </div>
        </Router>
        <CategoryAdd />
      </div>
    )
  }
}

export default App;
